.wrapper {
	min-width: 400px;
	background-color: var(--color-second);
	border: 4px solid var(--color-second);
	border-radius: 8px;
	padding: 16px;
	padding-top: 8px;

	display: flex;
	flex-direction: column;
	color: var(--color-font);
	gap: 4px;
}

.header {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	justify-content: space-between;
	gap: 8px;
}

.header :first-child {
	display: flex;
	align-items: center;
	gap: 8px;
}

.name {
	margin: 0px;
	font-weight: 500;
}

.symbol {
	margin: 0px;
	color: gray;
}

.infoTable {
	display: flex;
	flex-direction: column;
	gap: 8px;
}

.infoTable p {
	display: flex;
	justify-content: space-between;
}

.infoTable .price {
	font-size: 32px;
	padding: 8px 0px;
}

.footer {
	padding-top: 16px;
}

@media (max-width: 800px) {
	.wrapper {
		min-width: 50%;
		width: 100%;
	}
}
