.wrapper {
	display: flex;
	align-items: center;
	gap: 8px;

	padding: 8px;

	border: 1px gray solid;
	border-radius: 4px;
}

.buyButton {
	width: 60px;
	margin-left: auto;

	font-size: 1rem;

	visibility: hidden;
}

.wrapper:hover .buyButton {
	visibility: visible;
}

@media (max-width: 600px) {
	.buyButton {
		font-size: 0.8rem;
		width: 50px;
	}
}
