.wrapper {
	display: flex;
	flex: 1 0 300px;
	flex-direction: column;
	gap: 8px;

	max-width: fit-content;
	padding: 12px 16px;

	color: var(--color-font);
	background-color: var(--color-second);
	border: 4px solid var(--color-second);
	border-radius: 8px;
}

.wrapper:hover {
	box-shadow: 0px 0px 8px 0px black;
}

.label {
	font-size: 0.8rem;
	color: grey;
}

.values {
	display: flex;
	flex-direction: column;
	gap: 8px;
}

.diff {
	display: flex;
	gap: 4px;
}

.spentAmount,
.diff {
	font-size: 1rem;
	font-weight: bold;
}

.wrapper .label,
.spentAmount,
.diff {
	cursor: pointer;
}

@media (max-width: 800px) {
	.diff {
		flex-direction: column;
	}
}

@media (max-width: 450px) {
	.wrapper {
		flex: auto;
		max-width: none;
		width: inherit;
	}
	.values {
		flex-direction: row;
		gap: 8px;
	}
	.diff {
		flex-direction: row;
		gap: 8px;
	}
}
