.wrapper {
	background-color: var(--color-second);
	border: 4px solid var(--color-second);
	border-radius: 8px;
	padding: 20px;
	padding-top: 12px;
	min-width: 500px;
	color: var(--color-font);
	max-width: 1000px;
	max-height: 90vh;
	overflow-y: auto;

	display: grid;
	gap: 8px;
}

.header {
	display: flex;
	gap: 12px;
	align-items: center;
	font-size: 1.5rem;
}

.content {
	display: flex;
	gap: 8px;
	flex-wrap: wrap;
}

.priceSummary {
	flex: 1 0 300px;

	display: flex;
	flex-direction: column;
	font-size: 2.4rem;
}

.priceSummary__label {
	font-size: 1.2rem;
	color: grey;
}

.price {
	font-weight: bold;
}

.diff {
	font-size: 1.2rem;
	font-weight: bold;
}

.perCoinList {
	max-height: 400px;
	overflow-y: auto;
	padding-right: 16px;
	padding-bottom: 4px;
}

.perCoinList__label {
	font-size: 0.8rem;
	color: grey;
}

.favoritesList__label {
	font-size: 0.8rem;
	color: grey;
}

.perCoinList,
.favoritesList {
	flex: 1 0 400px;
	max-width: 100%;

	display: flex;
	flex-direction: column;
	gap: 8px;
}

@media (max-width: 600px) {
	.wrapper {
		min-width: inherit;
	}
	.header {
		font-size: 1.4rem;
	}
	.priceSummary__label {
		font-size: 1rem;
	}
	.priceSummary {
		font-size: 2rem;
	}
	.diff {
		font-size: 1rem;
	}
}
