.wrapper {
	position: fixed;
	padding: 16px;
	inset: 0;

	transition: all 0.25s 1;
}

.backdrop {
	position: absolute;
	inset: 0;

	background: var(--shadow-modal);
	backdrop-filter: blur(2px);
}

.drawer {
	display: flex;
	justify-content: center;
	inset: 1em 1em;

	transition: all 0.25s;
	animation: slideIn 500ms cubic-bezier(0.14, 0.78, 0.36, 1);

	position: absolute;
	flex-direction: column;
	height: fit-content;
	width: fit-content;

	margin: auto;
	align-items: center;
}

@keyframes slideIn {
	0% {
		opacity: 0;
		transform: translateY(-30px);
	}
	100% {
		opacity: 1;
		transform: none;
	}
}
