.wrapper {
	background-color: var(--color-second);
	border: 4px solid var(--color-second);
	border-radius: 8px;
	padding: 16px;
	padding-top: 8px;
	min-width: 700px;
	min-height: 400px;
}

.header {
	display: flex;
	color: var(--color-font);
	flex-direction: row;
	justify-content: flex-end;
	gap: 8px;
}

@media (max-width: 1300px) {
	.wrapper {
		min-width: 400px;
		width: 100%;
	}
}

@media (max-width: 900px) {
	.wrapper {
		min-height: auto;
	}
}
